<template>
  <div class="home-container">
    <van-nav-bar title="标题" :fixed="true"/>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" loosing-text>
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo v-for="item in artlist" :key="item.id"
        :title="item.title"
        :author="item.aut_name"
        :cmtCount="item.comm_count"
        :pubdate="item.pubdate"
        :cover="item.cover"
        ></ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import { getarticleAPI } from '@/API/articleAPI'
import ArticleInfo from '@/components/article/articleinfo'
export default {
  name: 'Home',
  data() {
    return {
      page: 10,
      limit: Date.now(),
      artlist: [],
      loading: true,
      finished: false,
      isLoading: false
    }
  },
  created() {
    this.articleList()
  },
  methods: {
    async articleList(ifJudge) {
      const { data } = await getarticleAPI(this.page, 165483514 + Math.floor(Math.random() * (9999 - 999)) + 1000)
      if (ifJudge) {
        this.artlist = [...data.data.results, ...this.artlist]
        this.isLoading = false
      } else {
        this.artlist = [...this.artlist, ...data.data.results]
      }
      this.loading = false
      if (data.data.results.length === 0) {
        this.finished = true
      }
    },
    onLoad() {
      this.page += 1
      this.articleList()
    },
    onRefresh() {
      this.page++
      this.articleList(true)
    }
  },
  components: {
    ArticleInfo
  }
}
</script>
<style lang="less" scoped>
  .home-container{
    padding: 46px 0 50px 0;
  }
  .van-nav-bar{
    background-color: rgb(37, 176, 231);
  }
  .root {
    --van-nav-bar-arrow-size: 100px;
  }
  /deep/ .van-nav-bar__title{
    color: white;
  }
</style>
